import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Modulo_indicadores } from '../../../modelo/Modulo_indicadores'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'modulo_indicadores-dados-edit',   templateUrl: 'modulo_indicadores_dadosEdit.component.html' }) export class Modulo_indicadoresDadosEditComponent implements OnInit {    modulo_indicadores: Modulo_indicadores;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.modulo_indicadores.Modulo_indicadores";                 let id = +params['id'];         this.modulo_indicadores = new Modulo_indicadores();         this.modulo_indicadores.cd_modulo_indicadores = id;         o.objetoJson = this.modulo_indicadores;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a modulo_indicadores. ';       }      this.modulo_indicadores = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


